import moment from 'moment';

export default class Util{
    getDate(str){
        var date = moment(str);
        var dateComponent = date.utc().format('YYYY-MM-DD');

        return dateComponent;
    }

    getTime(str){
        var date = moment(str);
        var timeComponent = date.utc().format('HH:mm:ss');

        return timeComponent;
    }
}